<template>
  <div class="hello">
    <div class="d-flex flex-column flex-md-row align-items-center p-3 px-md-4 mb-3 bg-white border-bottom shadow-sm">
      <h5 class="my-0 mr-md-auto font-weight-normal">Simulação</h5>
      <!--
      <nav class="my-2 my-md-0 mr-md-3">
      <a class="p-2 text-dark" href="#">Features</a>
    </nav> -->
    <b-col>{{msg}}</b-col>
    <div>
      <button class="btn btn-info btn-sm ml-2" @click="$refs.fileInput1.click()">Carregar Matrizes</button>
      <input type="file" id="fileInput1" ref="fileInput1" style="display:none;" @change="loadFileMat"/>
      <button class="btn btn-info btn-sm ml-2" @click="$refs.fileInput2.click()">Carregar Equivalências</button>
      <input type="file" id="fileInput2" ref="fileInput2" style="display:none;" @change="loadFileEq"/>
    </div>
  </div>
  <div class="row">
    <div class="col-2">
      <div class="form-group ml-2 mr-2 text-left">
        <label>Cursos</label>
        <select class="form-control form-control-sm" v-model="cursoSel" @change="mudouCurso()">
          <option v-for="(item,index) in cursos" :key="item.CURSO" :value="index">{{item.CURSO}}</option>
        </select>
      </div>
      <div class="form-group ml-2 mr-2 text-left">
        <label>Grades</label>
        <select class="form-control form-control-sm" v-model="gradeSel" @change="mudouGrade()">
          <option v-for="(item,index) in cursos[cursoSel].grades" :key="index" :value="index">{{item.VERSAO_GRADE}} - {{item.ies.textoIes}}</option>
        </select>
      </div>
    </div>
    <div class="col-9">
      <table class="" style="font-size: 9pt;">
        <thead>
          <tr>
            <th scope="col">Mod.</th>
            <th scope="col">Cod.Disc.</th>
            <th scope="col">Nome</th>
            <th scope="col">CH</th>
            <th scope="col">UC1</th>
            <th scope="col">UC2</th>
          </tr>
        </thead>
        <tbody>
          <tr :class="'linha linha'+item.MODULO" v-for="(item,index) in cursos[cursoSel].grades[gradeSel].disciplinas" :key="index">
            <td class="linha text-left">{{item.MODULO}}</td>
            <td class="linha text-left">{{item.COD_DISCIPLINA}}</td>
            <td class="linha text-left">{{item.NOME_DISCIPLINA}}</td>
            <td class="linha text-left" style="font-size: 6pt; white-space:nowrap;">{{item.DSC_CH}}</td>
            <td class="linha text-left text-primary">{{item.ucs.codUC1}} {{item.ucs.nomeUC1}}</td>
            <td class="linha text-left text-primary">{{item.ucs.codUC2}} {{item.ucs.nomeUC2}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Simulacao',
  props: { },
  data() {
    return {
      msg: "",
      fileMat: null,
      fileEq: null,
      cursos: [ { grades: [ { disciplinas: [], ies: { textoIes: ""}  }] }],
      cursoSel: 0,
      gradeSel: 0,
    };
  },
  created: function() {
    var self = this;
    console.log("oi Simulacao");
  },
  methods: {

    fileLoader: function(file, func) {
      if (window.File && window.FileReader && window.FileList && window.Blob) {
        var fileTobeRead = file.files[0];
        var fileExtension = /text.*/;
        //Check of the extension match
        if(fileTobeRead.type.match(fileExtension)) {
          //Initialize the FileReader object to read the 2file
          var fileReader = new FileReader();
          fileReader.onload = function (e) {
            func(fileReader.result);
          }
          fileReader.readAsText(fileTobeRead);
        }
        else {
          alert("Arquivo(s) não suportado(s)");
        }
      }
    },

    loadFileEq: function(file) {
      var self = this;
      console.log("loadFileMat");
      var fileSelected = document.getElementById('fileInput2');
      self.msg = "carregando...";
      this.fileLoader(fileSelected, function(data) {
        //console.log(data);
        self.msg = "processando equivalências...";
        var vet = data.split("\n");
        //console.log(vet);
        var campos = vet[0].split("§");
        console.log(campos);
        for(var i=1; i<vet.length; i++) {
          var linha = vet[i].split("§");
          var nomeCurso = linha[1];
          var codDisc = linha[2];
          for(var k=0; k<self.cursos.length; k++) {
            if(self.cursos[k].CURSO == nomeCurso) {
              for(var j=1; j<self.cursos[k].grades.length; j++) {
                for(var m=1; m<self.cursos[k].grades[j].disciplinas.length; m++) {
                  var disc = self.cursos[k].grades[j].disciplinas[m].COD_DISCIPLINA;
                  if(disc == codDisc) {
                    var objUCs = {
                      codUC1: linha[6],
                      nomeUC1: linha[7],
                      codUC2: linha[8],
                      nomeUC2: linha[9],
                      codUC3: linha[10],
                      nomeUC3: linha[11]
                    };
                    self.cursos[k].grades[j].disciplinas[m].ucs = objUCs;
                  }
                }
              }
            }
          }
        }
        self.msg = "Concluído!";
      });
    },

    loadFileMat: function(file) {
      var self = this;
      console.log("loadFileMat");
      var fileSelected = document.getElementById('fileInput1');
      self.msg = "carregando...";
      this.fileLoader(fileSelected, function(data) {
        //console.log(data);
        self.msg = "processando cursos...";
        var vet = data.split("\n");
        //console.log(vet);
        var campos = vet[0].split("§");
        console.log(campos);
        var cursos = [];
        var cursosObj = [];
        var grades = [];
        var gradesObj = [];
        for(var i=1; i<vet.length; i++) {
          var linha = vet[i].split("§");
          var nomeCurso = linha[3];
          if(cursos.indexOf(nomeCurso) == -1) {
            cursos.push(nomeCurso);
            var obj = {};
            for(var k=0; k<campos.length; k++) {
              obj[campos[k]] = linha[k];
            }
            obj.grades = [];
            cursosObj.push(obj);
          }
        }
        //console.log(cursos);
        self.msg = "processando grades...";

        for(var i=1; i<vet.length; i++) {
          var linha = vet[i].split("§");
          var codGrade = linha[5];
          if((linha[7] == "Liberada") && (grades.indexOf(codGrade) == -1)) {
            grades.push(codGrade);
            var obj = {};
            for(var k=4; k<12; k++) {
              obj[campos[k]] = linha[k];
            }
            obj.curso = linha[3];
            obj.disciplinas = [];
            obj.ies = [];
            gradesObj.push(obj);
          }
        }

        for(var i=1; i<vet.length; i++) {
          var linha = vet[i].split("§");
          var codGrade = linha[5];
          var idx = grades.indexOf(codGrade);
          if(idx != -1) {
            var obj = {};
            for(var k=12; k<linha.length; k++) {
              obj[campos[k]] = linha[k];
            }
            obj.ucs = {
              codUC1: "",
              nomeUC1: "",
              codUC2: "",
              nomeUC2: "",
              codUC3: "",
              nomeUC3: ""
            };
            var discJaExiste = false;
            for(var j=0; j<gradesObj[idx].disciplinas.length;j++) {
              var disc = gradesObj[idx].disciplinas[j];
              if(obj.COD_DISCIPLINA == disc.COD_DISCIPLINA) {
                //ver se ies já foi inserida
                var iesok = false;
                for(var m=0;m<gradesObj[idx].ies.length;m++) {
                  if(gradesObj[idx].ies[m].COD_GRADE == linha[4]) {
                    iesok = true;
                    break;
                  }
                }
                if(!iesok) {
                  var iesObj = { "MARCA": linha[0], "IES": linha[1], COD_GRADE: linha[4] };
                  gradesObj[idx].ies.push(iesObj);
                }
                var discJaExiste = true;
                break;
              }
            }
            if(!discJaExiste) {
              gradesObj[idx].disciplinas.push(obj);
              //ordena as disciplinas
              gradesObj[idx].disciplinas.sort((a, b) => (a.MODULO > b.MODULO) ? 1 : -1)
            }
          }
        }
        console.log(gradesObj);
        self.msg = "organizando cursos...";

        for(var i=1; i<gradesObj.length; i++) {
          var curso = gradesObj[i].curso;
          var idx = cursos.indexOf(curso);
          gradesObj[i].ies.textoIes = "";
          for(var k=0; k<gradesObj[i].ies.length; k++) {
            if(k == 0) {
              gradesObj[i].ies.textoIes = gradesObj[i].ies[k].IES;
            } else {
              gradesObj[i].ies.textoIes = gradesObj[i].ies.textoIes + "," + gradesObj[i].ies[k].IES;
            }
          }
          cursosObj[idx].grades.push(gradesObj[i]);
        }
        console.log(cursosObj);
        self.msg = "concluído!";
        self.cursos = cursosObj;
        //var myJSON = JSON.stringify(cursosObj);
        //self.saveData(myJSON,"saida.json");
      });
    },

    saveData: function(data, fileName) {
      var a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      var blob = new Blob([data], {type: "text/plain;charset=utf-8"});
      var url = window.URL.createObjectURL(blob);
      a.href = url;
      a.download = fileName;
      a.click();
      window.URL.revokeObjectURL(url);
    },

    mudouCurso: function() {
      console.log(this.cursoSel);
    },

    mudouGrade: function() {
      console.log(this.gradeSel);
    }

  }
}
</script>

